<template>
  <div>
    login
    <el-button @click="getDa">getData</el-button>
    <el-button @click="getLocal">getLocal</el-button>
    <el-button @click="getheight">getheight</el-button>
    <el-button @click="testApi">testApi</el-button>aa
    <el-button @click="testlogiin">testlogiin</el-button>aa
    <el-button @click="testmenu">testmenu</el-button>aa
    <el-button @click="getCooikes">getCooikes</el-button>aa
  </div>
</template>
<script>
export default {
  name: 'login',
  methods:{
    getCooikes(){
      console.log('cookie',document.cookie);
    },
    testmenu(){
       get('/pms_api/menu').then(data => {
        console.log('data',data)
      }).catch(err => {
        console.log('err',err)
      })
    },
    testlogiin(){
      // /pms_api/menu?JSESSIONID=f76186c2-5401-435c-b231-f6d4cb81a2f9
      let params={username:'admin',password:'admin'}
      post('/pms_api/login',params).then(data => {
        console.log('data',data)
      }).catch(err => {
        console.log('err',err)
      })
    },
    getDa(){
      let params={key:'b1b6820a0e81fe6ebfdbf503e38c91e7phone',phone:'15217637322'}
      post('/admin_api/mobile/get',params).then(data => {
        console.log(data)
      }).catch(err => {
        console.log(err)
      })
    },
    getLocal(){
      this.$axios.get('http://localhost:8080/static/data.json').then(data => {
        console.log(data)
      }).catch(err => {
        console.log(err)
        // this.$message.error('请求出错2')
      })
    },
    testApi(){
      let permsItem={permissionId:'1000001574876516',name:'/test4'}
      post(`/pms_api/permission/edit`,permsItem).then(res => {
          console.log('res',res)
          // this.cancelEdit()
      }).catch(err => {
          console.log(err)
      })
    },
    getheight(){
      console.log('height',document.documentElement.clientHeight);
    }
  }
}
</script>
<style>

</style>



